import {SUMAR, TOGGLE} from './Tipos'

const inicial = {contar:88, mostrar:false}

const reductor = (estado = inicial, accion) => {
    switch (accion.type) {
        case SUMAR: return {...estado, contar: estado.contar + accion.cuanto}
        case 'SUMAR': return {...estado, contar: estado.contar + accion.cuanto}
        case TOGGLE: return {...estado, mostrar: !estado.mostrar}
        default:
            return estado
    }
}

export default reductor